import { useParams } from "react-router"
import useFetch from "../useFetch";
import './Movie.css';

const MovieDetails = () => {
    const { id } = useParams();
    const { data: movie, error, isPending } = useFetch('http://localhost:8000/movies/' + id);

    return(
        <div className="movie-details">
            {isPending && <div>Loading...</div>}
            {error && <div>{error}</div>}
            {movie && (
                <>
                <img src={movie.image}/>
                <h3>Black Comedy,Action,Crime</h3>
                <h2>In Bruges</h2>
                <p>After a particularly difficult job, hit men Ray (Colin Farrell) and Ken (Brendan Gleeson) head to Belgium to hide out until things cool down. Ray hates the medieval city they land in, but Ken finds its beauty and peacefulness enchanting.</p>
                </>
            )}
        </div>
    )
}

export default MovieDetails;

